              //   }
              // })

              // _this.itemMap.set("itemPhaseDet",arr)

            }
        })
      },
    //下载组长汇总意见
    downloadLeaderAdvice(){
       let _this = this
       exportReport({"feaAuditid":_this.hQParam.auditFileId}).then(response=>{
          let filename = "评审意见信息模版.doc"
          _this.loading = false
          console.log('download res',response);
          //通过后台返回 的word文件流设置文件名并下载
          var blob = new Blob([response.data], { type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document;charset=utf-8'}); //application/vnd.openxmlformats-officedocument.wordprocessingml.document这里表示doc类型
          var downloadElement = document.createElement('a');
          var href = window.URL.createObjectURL(blob); //创建下载的链接
          downloadElement.href = href;
          downloadElement.download =filename; //下载后文件名
          document.body.appendChild(downloadElement);
          downloadElement.click(); //点击下载
          document.body.removeChild(downloadElement); //下载完成移除元素
          window.URL.revokeObjectURL(href); //释放掉blob对象
       })
    },
    //会签
    hQ(){
      let _this = this
      _this.loadingBtn2 = true
      let formData = new FormData()
      formData.append("id",_this.hQParam.id);
      // formData.append("agreeType",_this.hQParam.agreeType);
      // formData.append("auditOpinion",_this.hQParam.auditOpinon);
      if(_this.hQParam.agreeType==""){
                this.$message({
                  showClose: true,
                  message: "请选择会签意见",
                  type: 'warning'
                });
                _this.loadingBtn2 = false
                return false
        }
      hQ(formData).then(response=>{
            if(response.data.successful){
                this.$message({
                  showClose: true,
                  message: "会签成功",
                  type: 'success'
                });
                _this.dialogFormVisible = false
                _this.loadingBtn2 = false
                _this.currentPage = 1;
                _this.getTable(_this.currentPage,_this.currentNum)
            }else{
                this.$message({
                  showClose: true,
                  message: "会签失败",
                  dangerouslyUseHTMLString: true,
                  type: 'error'
                });
                _this.loadingBtn2 = false
            }
        })
